.about-wrap .theme-screenshot {
  color: #eee;
  transition: all 0.5s;
}

.about-wrap .theme-screenshot:hover {
  color: #fff;
}

.about-wrap .theme-screenshot i {
  text-align: center;
  display: block;
  margin: 10px;
  font-size: 90px;
  line-height: 200px;
  transition: all 0.5s ease;
}

.about-wrap .theme-browser .theme {
  transition: all 0.5s;
}

.about-wrap .theme-browser .theme:hover .theme-screenshot i {
  transition: all 0.5s;
  font-size: 120px;
}

.about-wrap .theme-browser .theme .theme-screenshot::after {
  content: inherit;
  display: inherit;
  padding-top: inherit;
}

.about-wrap .theme-screenshot figure {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  margin: 0;
}

.about-wrap .theme-screenshot figure i {
  position: relative;
  display: block;
  font-size: 11em;
  opacity: 0.8;
}

.about-wrap .theme-screenshot figure figcaption {
  padding: 15px;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.25em;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.about-wrap .theme-screenshot figure figcaption::before,
.about-wrap .theme-screenshot figure figcaption::after {
  pointer-events: none;
}

.about-wrap .theme-screenshot figure figcaption,
.about-wrap .theme-screenshot figure figcaption > a {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.about-wrap .theme-screenshot figure figcaption > a {
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0;
}

.about-wrap .theme-screenshot figure p {
  margin: 0;
  letter-spacing: 1px;
  font-size: 68.5%;
}

.about-wrap .theme-screenshot figure i {
  opacity: 0.7;
  transition: opacity 0.35s;
}

.about-wrap .theme:hover figcaption::before,
.about-wrap .theme:hover figcaption::after {
  opacity: 1;
  transform: scale(1);
}

.about-wrap .theme:hover i {
  opacity: 0.3;
}

.about-wrap .theme:hover p {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.3);
}

.about-wrap figure figcaption::before,
.about-wrap figure figcaption::after {
  position: absolute;
  top: 15px;
  right: 15px;
  bottom: 15px;
  left: 15px;
  content: "";
  opacity: 0;
  transition: opacity 0.35s, transform 0.35s;
}

.about-wrap figure figcaption::before {
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: scale(0, 1);
}

.about-wrap figure figcaption::after {
  border-right: 1px solid #fff;
  border-left: 1px solid #fff;
  transform: scale(1, 0);
}

.about-wrap figure p {
  padding: 10px;
  text-transform: none;
  opacity: 0;
  margin-top: 0 !important;
  transition: opacity 0.35s, transform 0.35s;
  transform: translate3d(0, 20px, 0);
}

